import { Injectable } from '@angular/core';
import { MeetingRoom } from './meeting-room.model';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class MeetingRoomsService {
  formData: MeetingRoom;
  apiUrl: string = '';
  list: MeetingRoom[];
  constructor(private http: HttpClient) { 
    this.apiUrl = environment.production ? 'http://localhost/MyWebApi/api/meetingrooms' : 'https://localhost:44336/api/MeetingRooms';
  }
  getList() {

    this.http.get<MeetingRoom[]>(this.apiUrl)
      .toPromise()
      .then(resp => this.list = resp as MeetingRoom[]);

  }

  getById(id: number) {
    this.http.get<MeetingRoom[]>(this.apiUrl + `/${id}`)
      .toPromise()
      .then(resp => this.list = resp as MeetingRoom[]);
  }

  postMeetingRoom() {
    return this.http.post(this.apiUrl, this.formData);
  }
  putMeetingRoom() {
    return this.http.put(this.apiUrl + '/' + this.formData.ID, this.formData);
  }
  deleteMeetingRoom(id) {
    return this.http.delete(this.apiUrl + '/' + id);
  }
}
